<template>
	<view class="map-content">
		<canvas canvas-id="canvasColumn" id="canvasColumn" class="map" ></canvas>
	</view>
</template>

<script>
	import uCharts from '../u-charts.js'
	var _self;
	var canvaColumn = null;   
	export default {
		data() {
			return {
				cWidth: '',
				cHeight: '',
				pixelRatio: 1,
			}
		},
		mounted() {
			_self = this;
			this.cWidth=uni.upx2px(710);
			this.cHeight=uni.upx2px(400);
			this.getServerData();
		},
		methods: {
			getServerData(){
				let LineA={categories:[],series:[]};   //创建数组
				LineA.categories= ["宁波市-浙江", "杭州市-浙江", "金华市-浙江"]  
				LineA.series=[
					{
						"name":"成功次数",
						"data":[20,8,25],
					},
				];
				_self.showColumn("canvasColumn",LineA);  // 调用实例化uChart方法，进行传值
			},
			
			showColumn(canvasId,chartData){
				canvaColumn = new uCharts({
					$this: _self,
					canvasId: canvasId,
					type: 'column',
					padding: [15,5,0,15],
					legend: {
						show: false,
						// padding:5,
						// lineHeight:11,
						// margin:0,
					},
					fontSize: 11,
					background: '#FFFFFF',
					pixelRatio: _self.pixelRatio,
					animation: true,
					categories: chartData.categories,
					series: chartData.series,
					xAxis: {
						//纵向辅助线，关闭
							disableGrid: true,
							itemCount: 5, //x轴单屏显示数据的数量，默认为5个
							scrollShow: false, //新增是否显示滚动条，默认false
							scrollAlign: 'left', //滚动条初始位置
							scrollBackgroundColor: '#F7F7FF', //默认为 #EFEBEF
							scrollColor: '#DEE7F7', //默认为 #A6A6A6
						},
					yAxis: {
						//横向辅助线，默认开启，样式为虚线
							gridType: 'dash'
					},
					dataLabel: true,
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
						column: {
							type:'group',
							width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
						}
					  }
				});
			},
		}
	}
</script>

<style lang="scss">
	.map-content {
		.map {
				width: 710upx;
				height: 400upx;
			}
	}
</style>